import React from 'react';
import { Cell, Switch, platform, IOS, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

// иконки

//иконки назад
import Icon28ReplyOutline from '@vkontakte/icons/dist/28/reply_outline';

const osname = platform();

const Notify = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="edit">
        {osname === IOS ? <Icon28ReplyOutline/> : <Icon28ReplyOutline/>}
    </PanelHeaderButton>}
        >
         Уведомления
        </PanelHeader>
        <Group>
        <Cell asideContent={<Switch />}description="Разрешение на уведомления">Уведомления</Cell>
    </Group>
    </Panel>
);

export default Notify;
