import React from 'react';
import {  Separator, Cell, platform, IOS, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

// иконки
import Icon28Notifications from '@vkontakte/icons/dist/28/notifications';
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline';

//иконки назад
import Icon28ReplyOutline from '@vkontakte/icons/dist/28/reply_outline';

const osname = platform();

const Edit = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ReplyOutline/> : <Icon28ReplyOutline/>}
    </PanelHeaderButton>}
        >
         Настройки
        </PanelHeader>
        <Group>
        <Cell expandable before={<Icon28Notifications />} >Уведомления</Cell>
        <Separator style={{ margin: '12px 0' }} />
        <Cell expandable before={<Icon28ArticleOutline />} onClick={props.go} data-to="info">О приложении</Cell>

    </Group>
    </Panel>
);

export default Edit;
